

























import Vue from 'vue'
import Component from 'vue-class-component'
import firebase from '@/firebase.config'

@Component
export default class Login extends Vue{
	email: string = ""
	password: string = ""

	login(e: Event) {
		firebase.auth.signInWithEmailAndPassword(this.email, this.password).then(user => {
			this.$router.push('/')
		})
		e.preventDefault();
	}
}
